  font-family: DM Sans, sans-serif;
  position: relative;
  overflow-x: hidden;
}

.nav {
  width: 100%;
  height: 50px;
  background-color: #fff;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
  position: relative;
}

.big-screen-nav-list {
  width: 100%;
  padding-top: 15px;
  display: flex;
}

.big-screen-nav-list-item {
  text-align: center;
  white-space: nowrap;
  margin-left: 15px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}

.big-screen-nav-list-item a {
  color: #ae1332;
  text-decoration: none;
}

.logo-section {
  width: 100%;
  position: relative;
}

.logo-icon {
  height: 16px;
  width: 16px;
}

.logo-text {
  color: #ae1332;
  white-space: nowrap;
  margin-top: -1px;
  margin-left: 8px;
  font-size: 14px;
  font-weight: 700;
}

.menu-actions {
  width: 100%;
  position: relative;
}

.hamburger-icon {
  height: 14px;
  width: 20px;
  cursor: pointer;
  margin-top: 15px;
  margin-right: 35px;
  display: none;
  position: absolute;
  right: 0;
}

.close-icon {
  height: 24px;
  width: auto;
  cursor: pointer;
  margin-top: 10px;
  margin-right: 35px;
  display: none;
  position: absolute;
  right: 0;
}

.bag-icon {
  height: 23.33px;
  width: auto;
  margin-top: 11px;
  margin-right: 35px;
  position: absolute;
  right: 0;
}

.small-screen-nav-list-wrapper {
  height: 100vh;
  width: 100vw;
  text-align: center;
  background-color: #ae1332;
  margin-top: 50px;
  display: none;
  position: absolute;
  top: 0;
}

.small-screen-nav-list {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.small-screen-nav-list-item {
  margin-bottom: 15px;
}

.small-screen-nav-list-item a {
  color: #fff;
  text-decoration: none;
}

.small-screen-bag-icon {
  filter: brightness(0) saturate() invert() sepia(0%) saturate(0%) hue-rotate(239deg) brightness(105%) contrast(105%);
}

.slide-from-right {
  animation: slideRightToLeft .25s ease-in-out forwards;
}

.slide-from-left {
  animation: slideLeftToRight .25s ease-in-out forwards;
}

@media screen and (max-width: 768px) {
  .nav {
    width: 767px;
  }
}

@media screen and (max-width: 375px) {
  .hamburger-icon {
    display: block;
  }

  .big-screen-nav-list {
    display: none;
  }

  .logo {
    margin-top: 15px;
    display: flex;
  }

  .bag-icon {
    display: none;
  }
}

@media screen and (min-width: 376px) {
  .hamburger-icon, .close-icon {
    display: none !important;
  }

  .small-screen-nav-list-wrapper {
    height: 100vh;
    overflow: hidden;
    display: none !important;
  }

  .logo {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

@keyframes slideRightToLeft {
  from {
    transform: translateX(100%);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes slideLeftToRight {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(100%);
  }
}

